/* Contact section */
#contact {
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: 10% 80%;
  grid-template-areas:
  "contact-heading contactGrid";
}

#contact-heading {
grid-area: contact-heading;
}

#third-line {
grid-area: third-line;
}

#contactGrid {
grid-area: contactGrid;
display: grid;
grid-template-rows: 100%;
grid-template-columns: 25% 25% 25% 25%;
grid-template-areas:
"website github linkedin email";
}

#website {
grid-area: website;
}

#github {
grid-area: github;
}

#twitter {
grid-area: twitter;
}

#email {
grid-area: email;
}

.contact-grid-element a {
color: var(--bckgrnd-blck);
font-size: 16px;
border-bottom: 3px solid var(--bckgrnd-blck);
}

.contact-grid-element a:hover {
background-color: rgba(87, 168, 144, 0.336);
padding: 5px;
}