/* About Me section */
#about {
    /* width: 90%; */
    display: grid;
    grid-template-columns: 5% 5% 80%;
    grid-template-rows: 100%;
    grid-template-areas: "aboutHeading first-line aboutText";
    justify-items: center;
}

#aboutHeading {
    grid-area: aboutHeading;
    display: flex;
}

#first-line {
  grid-area: first-line;
}

#aboutText {
    grid-area: aboutText;
}

#aboutText > h2 {
    padding-bottom: 35px;
}

#aboutText > p {
    font-size: 15px;
}